<template>
  <div>
    <img src="../assets/images/adver.jpg" alt />
    <button @click="goHome">跳过{{num}}</button>
    <div class="img_logo">
      <!-- <h1>Jane Coffe</h1> -->
      <img src="../assets/images/logo.jpg" alt class="logo" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Advertpage",
  data() {
    return {
      num: 5,
      cleardata: null
    };
  },
  methods: {
    goHome() {
      this.$router.push("/home/classlist");
    }
  },
    created() {
      this.cleardata = setInterval(() => {
        if (this.num == 0) {
          this.$router.push("/home/classlist");
        } else {
          this.num--;
        }
      }, 1000);
    },
  // 销毁生命周期函数之前停止
  beforeDestroy() {
    //销毁vue数据与方法。组件结束执行
    // 定时器属于浏览器对象，属于window。
    clearInterval(this.cleardata);
  }
};
</script>

<style lang="less" scoped>
button {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.3);
  border: 0;
  padding: 2px 9px;
  border-radius: 15px;
  float: right;
  margin-right: 10px;
  margin-top: 5px;
  font-size: 12px;
}
img{
    width: 100%;
}
.img_logo {
  margin-top: 30px;
  text-align: center;
  h1 {
    font-size: 20px;
  }
  .logo {
    width: 80px;
    height: 100%;
    margin: 0 auto;
    margin-top: 10px;
  }
}
</style>